.Nav {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  background: #ff2020;
}

.Nav h3 {
  width: 40%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: rgba(255, 255, 255, 1);
  letter-spacing: 1.2px;
}

.Navigation {
  width: 60%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Navigation input {
  border: none;
  border-radius: 2px;
  width: 15vw;
  padding: 0.8vh 5vw;
  text-align: center;
}

.results {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  text-align: center;
}
.result {
  width: 20vw;
  height: 50vh;
  -ms-flex-positive: 1;
      flex-grow: 1;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5)));
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  background: -o-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
}
.result img {
  width: 100%;
  height: 50%;
}
.innerText {
  opacity: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.innerText h3 {
  margin: 0;
  letter-spacing: 1.1px;
}
.innerText h4 {
  font-weight: 400;
  width: 90%;
}
.innerText:hover {
  opacity: 1;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 32, 32, 0.7)), to(rgba(255, 32, 32, 0.99)));
  background: -webkit-linear-gradient(rgba(255, 32, 32, 0.7), rgba(255, 32, 32, 0.99));
  background: -o-linear-gradient(rgba(255, 32, 32, 0.7), rgba(255, 32, 32, 0.99));
  background: linear-gradient(rgba(255, 32, 32, 0.7), rgba(255, 32, 32, 0.99));
}

.buttons h5 {
  border: 1px solid #fff;
  line-height: 20px;
  padding: 1vh 4vw;
  color: #fff;
  cursor: pointer;
}
.buttons h5:hover {
  background: #fff;
  color: #000;
}

.buttons a {
  width: 50%;
  text-decoration: none;
}

@media only screen and (max-width: 992px) {
  .result {
    width: 33%;
  }
}
@media only screen and (max-width: 992px) {
  .result {
    width: 33%;
  }
}
@media only screen and (max-width: 768px) {
  .result {
    width: 50%;
  }
}
@media only screen and (max-width: 600px) {
  .result {
    width: 100%;
    margin: 0 0 0vh 0;
  }
}



